import { useState } from 'react'

export default function FilterMenuItem(props) {
  const [active, setActive] = useState(false);
  const { children } = props;

  const hadleButtonClick = () => {
    setActive(!active)

  }

  return (
    <menu className='filter__item'>
      <p className='filter__item-name text_color_gold'>{props.name}</p>
      <button className='filter__item-button' type='button' onClick={hadleButtonClick} />
      {active && children}
    </menu>
  )
}