import logo from '../images/logo.svg';
import '../blocks/header/header.css'

import { Link, useLocation } from 'react-router-dom';
import HeaderMenu from './HeaderMenu';

export default function Header() {
  const location = useLocation();

  return (
    <header className={'header ' + (location.pathname !== '/catalogue' ? 'header_underline_show' : 'header_underline_hidden')}>
      {
        location.pathname !== '/catalogue' && (
          <>
            <img className='header__logo' alt='Логотип' src={logo} />
            <nav className='header__links'>
              <Link className='header__link' to='/legend'>Легенда</Link>
              <Link className='header__link' to='/catalogue'>Каталог</Link>
              <Link className='header__link' to='/aboutus'>О нас</Link>
            </nav>
          </>
        )
      }
      <HeaderMenu />
    </header>
  );
}